import React, {
  useEffect,
  useMemo,
  useState,
  useRef,
  useCallback,
} from "react";
import "./FileUpload.css";
import { Dropdown, Form, Stack } from "react-bootstrap";
import CustomButton from "../../components/Button/Button";
import FileUpload from "../../components/FileUploader/FileUploader";

const MassUpload = () => {
  const handleFileUpload = (files) => {
    console.log("Uploaded files:", files);
  };

  return (
    <>
      <Stack className="mt-3" gap={4}>
        {/* choose opration and download template stack */}

        <Stack direction="horizontal">
          <Dropdown>
            <Dropdown.Toggle variant="outline-primary" id="dropdown-basic">
              Choose Operations
            </Dropdown.Toggle>

            <Dropdown.Menu>
              <Dropdown.Item href="#/action-1">Action</Dropdown.Item>
              <Dropdown.Item href="#/action-2">Another action</Dropdown.Item>
              <Dropdown.Item href="#/action-3">Something else</Dropdown.Item>
            </Dropdown.Menu>
          </Dropdown>

          <CustomButton
            variant="link ms-auto"
            // onClick={handleSelectAll}
            text="Downlad Template"
          />
        </Stack>

        {/* drag and drop conatiner */}
        <FileUpload
          fileTypes={["XLSX"]}
          multiple={false}
          onUpload={handleFileUpload}
        />
        {/* Form-cta submit*/}

        <Stack direction="horizontal">
          <Form.Check type="checkbox" label="background" />
          <CustomButton
            variant="primary ms-auto"
            // onClick={handleSelectAll}
            text="Submit"
          />
        </Stack>
      </Stack>
      {/* <CustomModal show={showModal} handleClose={handleModalClose} /> */}
    </>
  );
};

export default MassUpload;
